body {
    .container {
        background-color: orangered;
    }
}

@mixin left-top {
    position: absolute;
    top: 0;
    left: 0;
    @content;
}

.container-asdf {
    width: 100px;
    height: 100px;
    @include left-top;
}

.box-asdf {
    width: 200px;
    height: 300px;
    @include left-top {
        bottom: 0;
        right: 0;
        margin: auto;
    }
}

